// export default About;
// app/about/page.jsx

function About() {
  const team = [
    {
      name: "Sarah Chen",
      role: "Founder & CEO",
      bio: "Former product lead at tech startups. Passionate about community empowerment.",
      image: "/team/sarah.jpg" // placeholder
    },
    {
      name: "David Omondi",
      role: "Head of Community",
      bio: "Local business advocate with 8+ years in community organizing.",
      image: "/team/david.jpg"
    },
    {
      name: "Amina Hassan",
      role: "Product Designer",
      bio: "UX specialist focused on creating inclusive, accessible designs.",
      image: "/team/amina.jpg"
    }
  ];

  const milestones = [
    { year: "2023", event: "Locafind founded in Nairobi" },
    { year: "2024 Q1", event: "Launched MVP with 500+ listings" },
    { year: "2024 Q2", event: "Reached 10,000 active users" },
    { year: "2024 Q3", event: "Expanded to 5 major cities" }
  ];

  return (
    <Layout>
      {/* Hero Section */}
      <section className="relative bg-gradient-to-br from-[#37353E] to-[#715A5A] text-white py-24">
        <div className="container mx-auto px-4 text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-6">
            Building Community
            <span className="block text-[#D3DAD9]">Through Local Discovery</span>
          </h1>
          <p className="text-xl text-[#D3DAD9] max-w-3xl mx-auto leading-relaxed">
            We're on a mission to connect neighborhoods with the local businesses that make them special. 
            Because every community deserves to know what's right around the corner.
          </p>
        </div>
        
        {/* Background Pattern */}
        <div className="absolute inset-0 opacity-10">
          <div className="absolute inset-0" style={{
            backgroundImage: `radial-gradient(circle at 25px 25px, white 2%, transparent 0%), radial-gradient(circle at 75px 75px, white 2%, transparent 0%)`,
            backgroundSize: '100px 100px'
          }}></div>
        </div>
      </section>

      {/* Mission & Values */}
      <section className="py-20 bg-white">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 max-w-6xl mx-auto">
            
            {/* Mission */}
            <div className="space-y-8">
              <div>
                <span className="text-[#715A5A] font-semibold text-lg">Our Why</span>
                <h2 className="text-4xl font-bold text-[#37353E] mt-2">The Problem We Solve</h2>
              </div>
              
              <div className="space-y-6">
                <p className="text-lg text-[#44444E] leading-relaxed">
                  In today's digital age, small local businesses are becoming increasingly invisible. 
                  While global platforms focus on large chains and restaurants, the mama mbogas, 
                  local repair shops, and neighborhood services that form the backbone of our communities 
                  remain undiscovered.
                </p>
                
                <p className="text-lg text-[#44444E] leading-relaxed">
                  We believe that technology should serve communities, not replace them. Locafind 
                  bridges this gap by creating a platform where local businesses can be found and 
                  communities can thrive together.
                </p>
              </div>

              {/* Impact Stats */}
              <div className="grid grid-cols-2 gap-6 pt-6">
                <div className="text-center">
                  <div className="text-3xl font-bold text-[#715A5A]">85%</div>
                  <div className="text-[#44444E] text-sm">of listed businesses were previously invisible online</div>
                </div>
                <div className="text-center">
                  <div className="text-3xl font-bold text-[#715A5A]">3.2x</div>
                  <div className="text-[#44444E] text-sm">more customer visits for verified businesses</div>
                </div>
              </div>
            </div>

            {/* Values */}
            <div className="space-y-8">
              <div>
                <span className="text-[#715A5A] font-semibold text-lg">Our Approach</span>
                <h2 className="text-4xl font-bold text-[#37353E] mt-2">Core Values</h2>
              </div>

              <div className="space-y-6">
                {[
                  {
                    icon: "ðŸ‘¥",
                    title: "Community First",
                    description: "Every feature is designed with local communities in mind. We build with, not for."
                  },
                  {
                    icon: "ðŸ”",
                    title: "Radical Transparency",
                    description: "Open verification processes and clear data practices you can trust."
                  },
                  {
                    icon: "ðŸŒ±",
                    title: "Sustainable Growth",
                    description: "We grow with our communities, ensuring quality over rapid expansion."
                  },
                  {
                    icon: "ðŸŽ¯",
                    title: "Local Empowerment",
                    description: "Tools and insights that help small businesses compete and thrive."
                  }
                ].map((value, index) => (
                  <div key={index} className="flex items-start space-x-4 p-4 rounded-2xl hover:bg-[#D3DAD9] transition-colors">
                    <div className="text-2xl flex-shrink-0">{value.icon}</div>
                    <div>
                      <h3 className="font-semibold text-[#37353E] text-lg mb-1">{value.title}</h3>
                      <p className="text-[#44444E]">{value.description}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Timeline */}
      <section className="py-20 bg-[#D3DAD9]">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto">
            <div className="text-center mb-16">
              <h2 className="text-4xl font-bold text-[#37353E] mb-4">Our Journey</h2>
              <p className="text-xl text-[#44444E]">From idea to community impact</p>
            </div>

            <div className="relative">
              {/* Timeline line */}
              <div className="absolute left-1/2 transform -translate-x-1/2 w-1 bg-[#715A5A] h-full"></div>
              
              {/* Milestones */}
              <div className="space-y-12">
                {milestones.map((milestone, index) => (
                  <div key={index} className={`relative flex items-center ${index % 2 === 0 ? 'flex-row' : 'flex-row-reverse'}`}>
                    {/* Content */}
                    <div className={`w-1/2 ${index % 2 === 0 ? 'pr-12 text-right' : 'pl-12'}`}>
                      <div className="bg-white rounded-2xl p-6 shadow-lg border border-[#44444E]/10">
                        <div className="text-[#715A5A] font-semibold text-lg">{milestone.year}</div>
                        <div className="text-[#37353E] font-medium">{milestone.event}</div>
                      </div>
                    </div>
                    
                    {/* Dot */}
                    <div className="absolute left-1/2 transform -translate-x-1/2 w-4 h-4 bg-[#715A5A] rounded-full border-4 border-white shadow-lg"></div>
                    
                    {/* Spacer */}
                    <div className="w-1/2"></div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Team Section */}
      <section className="py-20 bg-white">
        <div className="container mx-auto px-4">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-[#37353E] mb-4">Meet Our Team</h2>
            <p className="text-xl text-[#44444E] max-w-2xl mx-auto">
              Passionate individuals dedicated to building tools that serve communities
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-5xl mx-auto">
            {team.map((member, index) => (
              <div key={index} className="text-center group">
                <div className="w-32 h-32 bg-[#D3DAD9] rounded-full mx-auto mb-6 overflow-hidden border-4 border-white shadow-lg group-hover:border-[#715A5A] transition-colors">
                  {/* Placeholder for team photo */}
                  <div className="w-full h-full bg-gradient-to-br from-[#37353E] to-[#715A5A] flex items-center justify-center text-white text-2xl">
                    {member.name.split(' ').map(n => n[0]).join('')}
                  </div>
                </div>
                <h3 className="text-xl font-bold text-[#37353E] mb-2">{member.name}</h3>
                <div className="text-[#715A5A] font-semibold mb-3">{member.role}</div>
                <p className="text-[#44444E] text-sm leading-relaxed">{member.bio}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 bg-gradient-to-br from-[#37353E] to-[#715A5A] text-white">
        <div className="container mx-auto px-4 text-center">
          <h2 className="text-4xl font-bold mb-6">Join Our Mission</h2>
          <p className="text-xl text-[#D3DAD9] mb-8 max-w-2xl mx-auto">
            Whether you're a user, business owner, or potential partner, there's a place for you in our community.
          </p>
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
            <button className="px-8 py-4 bg-white text-[#37353E] rounded-xl hover:bg-[#D3DAD9] transition-colors font-semibold">
              Explore Local Businesses
            </button>
            <button className="px-8 py-4 bg-transparent border-2 border-white text-white rounded-xl hover:bg-white hover:text-[#37353E] transition-colors font-semibold">
              Partner With Us
            </button>
          </div>
        </div>
      </section>
    </Layout>
  );
}
export default About;